import React, { Component } from 'react';
import BreadCrumb from "../../../common/bread";
import {Card, Input, List} from "antd";
import { connect } from "react-redux";
import { Link } from  "react-router-dom";
import { actionCreators } from "./store";
import Button from '../../../common/components/Button';
import './index.scss';

class groupMessage extends Component {
  render() {
    const { gonghaoList, nick_name, search, getMaterial, changeSearchInput } = this.props;
    return (
    	<div>
        <BreadCrumb/>
        <div className="container">
          <Input.Search
            placeholder="请输入公号名称"
            enterButton="Search"
            style={{width: 300}}
            onSearch={search}
            value={nick_name}
            onChange={changeSearchInput}
          />
          <div className="table-wrapper">
            <List
              grid={{gutter: 16, column: 4}}
              dataSource={gonghaoList}
              renderItem={item => (
                <List.Item>
                  <Card className="card-item">
                    <p>{item.nick_name}</p>
                    <ul>
                      <li onClick={() => getMaterial(item.code)}>同步素材</li>
                      <li><Link to={{pathname: "/admin/message/group/material", state: {code: item.code}}}>群发消息</Link></li>
                      <li><Link to={{pathname: "/admin/message/group/history", state: {code: item.code}}}>群发记录</Link></li>
                    </ul>
                  </Card>
                </List.Item>
              )}
            />
          </div>
        </div>
    	</div>
    )
  }

  componentDidMount() {
    this.props.getGroupGonghao()
  }
}

const mapState = state =>({
  gonghaoList: state.message.group.gonghaoList,
  nick_name: state.message.group.nick_name
});

const mapDispatch = dispatch => ({
  getGroupGonghao() {
    dispatch(actionCreators.getGroupGonghao())
  },
  search(nick_name) {
    dispatch(actionCreators.search(nick_name))
  },
  changeSearchInput(e) {
    dispatch(actionCreators.changeSearchInput(e.target.value))
  },
  getMaterial(code) {
    dispatch(actionCreators.getMaterial(code))
  }
});

export default connect(mapState, mapDispatch)(groupMessage);