import React, { Component } from 'react';
import { Card, List, Icon } from 'antd';
import "./index.scss";
import moment from 'moment';
import Button from "../../../pages/message/group/material";

class GraphicMessage extends Component {
  render() {
    const { messageList, syncMaterial, openModal, openTimingModal, copy, deleteMaterial } = this.props;

    return (
    	<div className="message-list-wrapper">
        <List grid={{gutter: 20, sm: 4, xxl: 6}} dataSource={messageList} renderItem={(item, index) => <List.Item key={item._id}>
          <Card className="message-list-card" title={
            <List className="message-list">
              <div className="update-time"><Icon type="sound" /> 更新于 {moment(item.update_time * 1000).format("YYYY-MM-DD hh-mm-ss")}</div>
              <List.Item key={item.content.news_item[0].thumb_media_id}>
                <div className="head-message">
                  <p>{item.content.news_item[0].title}</p>
                  <img src={item.content.news_item[0].local_img_path} alt=""/>
                </div>
              </List.Item>
              {item.content.news_item.slice(1).map(message => <List.Item className="border_btm" key={message.thumb_media_id}>
                <div className="rest-message">
                  <p>{message.title}</p>
                  <img src={message.local_img_path} alt=""/>
                </div>
              </List.Item>)}
            </List>}>
            <List grid={{gutter: 24, column: 3}} className="action-wrapper">
              <List.Item className="cursor border_btm" onClick={() => syncMaterial(item)}>同步</List.Item>
              <List.Item className="cursor border_btm" onClick={() => openModal(item)}>发送</List.Item>
              <List.Item className="cursor border_btm" onClick={() => openTimingModal(item)}>定时</List.Item>
              <List.Item className="cursor" onClick={() => copy(item._id)}>复制</List.Item>
              <List.Item className="cursor" onClick={() => deleteMaterial(item._id, index)}>删除</List.Item>
            </List>
          </Card>
        </List.Item>} />

    	</div>
    )
  }
}

export default GraphicMessage;